import { Component, OnInit } from "@angular/core";
import { ModalController } from "@ionic/angular";
import { ProductsService } from "./../../services/products.service";
import { Product } from "../../models/products";

@Component({
	selector: "app-cart-modal",
	templateUrl: "./cart-modal.page.html",
	styleUrls: ["./cart-modal.page.scss"]
})
export class CartModalPage implements OnInit {
	emptyCart: boolean;
	cart: Product[] = [];

	constructor(
		private cartService: ProductsService,
		private modalCtrl: ModalController
	) {}

	ngOnInit() {
		this.cart = this.cartService.getCart();
		this.emptyCart =
			this.cartService.getCartItemCount().value > 0 ? false : true;
		console.log(this.emptyCart);
	}

	getTotal() {
		return this.cart.reduce((i, j) => i + j.price * j.cartQty, 0);
	}

	closeModal() {
		this.modalCtrl.dismiss();
	}

	increaseItem(p) {
		this.cartService.addProducts(p);
	}
	decreaseItem(p) {
		this.cartService.decreaseProducts(p);
	}

	removeItem(p) {
		this.cartService.removeProduct(p);
	}
}
